* {
  margin: 0;
  padding: 0;
}
body {
  font-family: "Roboto", sans-serif;
}
/*-------------------Global variables-------------------*/
.error {
  color: red;
  font-weight: 600;
}

.hide {
  display: none;
}

.none {
  text-decoration: none;
}

.white {
  color: #fff;
}

.grey {
  color: #383636;
}

.disabled {
  pointer-events: none;
  cursor: none;
}
/*------------------Home page------------------------*/

.homePageBackground {
  background: #f2d333;
  min-height: 100vh;
}

.homePageContainer {
  width: 100%;
  display: flex;
  position: absolute;
  justify-content: center;
  top: 37.5%;
  text-align: center;
}

.homePageTitle {
  margin-left: -4%;
  text-align: center;
  color: #fff;
  font-weight: 900;
  font-size: 80px;
  -webkit-user-select: none; /* Chrome all / Safari all */
  -moz-user-select: none; /* Firefox all */
  -ms-user-select: none; /* IE 10+ */
  user-select: none;
  margin-bottom: 5%;
}

.homePageInput {
  position: relative;
}

.homePageFormContainer {
  position: relative;
  margin-left: 5%;
}

.homePageForm {
  width: 100px;
  height: 65px;
  border-radius: 20px;
}

.homePagePin {
  padding: 12px 20px;
  border-radius: 5px;
  outline: none;
  text-align: center;
  font-weight: 600;
  font-size: 18px;
  color: #a9a9a9;
  border: 1px solid #a9a9a9;
}

.homePagePinRED {
  padding: 12px 20px;
  border-radius: 5px;
  outline: none;
  text-align: center;
  font-weight: 600;
  font-size: 18px;
  color: red;
  border: 1px solid red;
}

.homePagePinGreen {
  padding: 12px 20px;
  border-radius: 5px;
  outline: none;
  text-align: center;
  font-weight: 600;
  font-size: 18px;
  color: green;
  border: 1px solid green;
}

.homePagePin placeholder {
  color: #a9a9a9;
}

.homePageBtn {
  margin-top: 12%;
  width: 250px;
  height: 45%;
  right: 50%;
  background: #383636;
  color: #fff;
  font-weight: 700;
  font-size: 18px;
  cursor: pointer;
  border: 1px solid #a3a3a3;
  border-radius: 3px;
  margin-left: 12.5%;
  display: block;
}

.homePageFooter {
  display: grid;
  position: absolute;
  width: 50%;
  padding: 0px 0;
  color: #fff;
  top: 90%;
  justify-content: center;
  margin-left: 0%;
  text-align: center;
}

.homePageTerms {
  margin-top: 4%;
}

.homePageError {
  margin-top: 5%;
}

.homePageBtns {
  display: grid;
  grid-template-columns: repeat(1fr);
}

.homePageCreate {
  background: #9cc93c;
  width: 250px;
  color: #fff;
  font-weight: 700;
  font-size: 18px;
  cursor: pointer;
  border: 1px solid #9cc93c;
  border-radius: 3px;
  display: block;
  margin-bottom: 5%;
  margin-left: 4.3%;
  height: 40px;
}

.homePageJoin {
  background: #383636;
  width: 250px;
  color: #fff;
  font-weight: 700;
  font-size: 18px;
  cursor: pointer;
  border: 1px solid #383636;
  border-radius: 3px;
  display: block;
  margin-bottom: 5%;
  margin-left: 4.3%;
  height: 40px;
}

.homePageJoinDisabled {
  background: #c5c4c4;
  width: 250px;
  color: #fff;
  font-weight: 700;
  font-size: 18px;
  cursor: pointer;
  border: 1px solid #c5c4c4;
  border-radius: 3px;
  display: block;
  margin-bottom: 5%;
  margin-left: 4.3%;
  height: 40px;
}

