/*-------------------------Lobby page---------------------*/
.lobbyPage {
  width: 100%;
  min-height: 200vh;
}

.lobbyCloseIcon {
  display: flex;
  color: #fff;
  float: right;
}

.lobbyPin {
  margin-left: 9%;
  display: grid;
  justify-content: center;
  text-align: center;
}

.lobbyPinH2 {
  margin-top: 50%;
  color: #fff;
  font-weight: 900;
  font-size: 35px;
  text-align: center;
}
.lobbyPinH1 {
  color: #fff;
  font-weight: 900;
  font-size: 55px;
  text-align: center;
}

.lobbyInvite {
  display: flex;
  background: #71c678;
  border: solid 1px #71c678;
  border-radius: 5px;
  font-size: 18px;
  margin-top: 3%;
  color: #fff;
  cursor: pointer;
  padding: 8px 4px;
  width: 150px;
  justify-content: center;
  align-items: center;
  margin: auto;
  outline: none;
}

.lobbyPlayers {
  display: grid;
  text-align: center;
  margin-top: 15%;
}

.lobbyPlayer {
  color: #fff;
}

.lobbyStart {
  display: grid;
  justify-content: center;
  margin: auto;
  margin-top: 21%;
  background: #f53b51;
  color: #fff;
  border: 1px solid #f53b51;
  width: 95%;
  height: 40px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 20px;
  font-weight: 900;
  outline: none;
}

.lobbyChat {
  display: grid;
  justify-content: center;
  margin: auto;
  margin-top: 3%;
  width: 100%;
  background: #383636;
  height: 44px;
}

.lobbyChatInput {
  width: 342px;
  margin-top: 7px;
  height: 30px;
  padding: 0 7px;
  border: none;
  border-radius: 20px;
  outline: none;
}

.lobbyPhoto {
  background: #f69e4e;
  max-height: 100vh;
  height: auto;
  overflow: auto;
}

.lobbyFacts {
  background: #95e79c;
  max-height: 100vh;
  height: auto;
  overflow: auto;
}

.lobbyDares {
  background: #41c4de;
  max-height: 100vh;
  height: auto;
  overflow: auto;
}

.lobbyTruth {
  background: #f79155;
  max-height: 100vh;
  height: auto;
  overflow: auto;
}

.mainLobby {
  background: #f2d333;
  max-height: 100vh;
  height: auto;
  overflow: auto;
}

/*---------------iPhone X in Landscape-----------------*/
@media only screen and (min-device-width: 375px) and (max-device-width: 812px) and (-webkit-device-pixel-ratio: 3) and (orientation: landscape) {
  .homePageFooter {
    padding: 0px 25%;
    margin-left: 5%;
  }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
  .homePageFooter {
    padding: 0px 25%;
    margin-left: 5%;
  }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  .homePageFooter {
    padding: 0px 25%;
    margin-left: 5%;
  }
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .homePageFooter {
    padding: 0px 20%;
    margin-left: 5%;
  }
}
